
import i18n from '@/i18n';
import {
  ARTIFACT_SET_MASTER_DUMMY,
  TCharacterInput,
  TRecommendation,
  ARRAY,
  getMaxConstellation,
  getMaxTalentLevel,
} from '@/input';
import {
  ELEMENT_BG_COLOR_CLASS,
  ELEMENT_COLOR_CLASS,
  ELEMENT_IMG_SRC,
  getArtifactIconUrl,
  IMG_SRC_DUMMY,
  RECOMMEND_ABBREV_EN_MAP,
  STAR_BACKGROUND_IMAGE_CLASS,
  TCharacterDetail,
  TWeaponDetail,
} from '@/master';
import {
  computed,
  defineComponent,
  nextTick,
  PropType,
  reactive,
  ref,
} from 'vue';
import CompositionFunction from './CompositionFunction.vue';

export default defineComponent({
  name: 'CharacterInput',
  props: {
    characterInput: {
      type: Object as PropType<TCharacterInput>,
      required: true,
    },
    recommendationList: {
      type: Array as PropType<TRecommendation[]>,
      required: true,
    },
    recommendation: {
      type: Object as PropType<TRecommendation>,
      required: true,
    },
    artifactSetSelectVisible: { type: Boolean },
    artifactScore: { type: Number, required: true },
  },
  emits: [
    'open:character-select',
    'open:character-info',
    'update:recommendation',
    'saveToStorage',
    'removeFromStorage',
    'open:weapon-select',
    'open:artifact-set-select',
    'open:artifact-detail-input',
    'update:character-input-character',
    'update:character-input-weapon',
    'open:character-info',
  ],
  setup(props, context) {
    const { displayName, targetValue } = CompositionFunction();

    const characterInputRea = reactive(props.characterInput);

    const storageOrRecommendationRef = ref('0');

    const displayBuildName = (item: TRecommendation) => {
      if (i18n.global.locale.value == 'ja-jp') return item.name;
      // if (item.name == '' + item.build['']) {
      //   return 'Your ' + displayName(item.build['']);
      // }
      if (item.overwrite) return item.name;
      let result = '';
      result += displayName(item.build['']);
      result += ' - ';
      if (item.build['1']) {
        result += displayName(item.build['1']);
      }
      if (
        item.build['2'] &&
        item.build['2'] != item.build['1']
      ) {
        result += '/';
        result += displayName(item.build['2']);
      }
      result += '[';
      for (const key of [
        '3',
        '4',
        '5',
      ]) {
        const statAndRarity = item.build[key].split('_');
        result += RECOMMEND_ABBREV_EN_MAP.get(statAndRarity[1]);
      }
      result += ']';
      return result;
    };

    const characterMaster = computed(() => characterInputRea.characterMaster);
    const weaponMaster = computed(() => characterInputRea.weaponMaster);
    const artifactSetMasters = computed(
      () =>
        characterInputRea.artifactSetMasters ?? [
          ARTIFACT_SET_MASTER_DUMMY,
          ARTIFACT_SET_MASTER_DUMMY,
        ]
    );

    const visionSrc = (item: TCharacterDetail) =>
      ELEMENT_IMG_SRC[item.] as string;
    const bgImageClass = (item: TCharacterDetail | TWeaponDetail) =>
      (' ' + STAR_BACKGROUND_IMAGE_CLASS[item.]) as string;
    const colorClass = (item: TCharacterDetail) =>
      ELEMENT_COLOR_CLASS[item.] as string;
    const bgColorClass = (item: TCharacterDetail) =>
      ELEMENT_BG_COLOR_CLASS[item.] as string;

    const saveDisabled = computed(() => {
      let result = characterInputRea.saveDisabled;
      if (
        characterInputRea.characterMaster. !=
        characterInputRea.weaponMaster.
      ) {
        result = true;
      }
      return result;
    });

    /**  */
    const saveOnClick = async () => {
      characterInputRea.saveDisabled = false; // 
      characterInputRea.removeDisabled = true; // 
      await nextTick();
      context.emit('saveToStorage', characterInputRea.buildname);
    };

    /**  */
    const removeOnClick = async () => {
      characterInputRea.removeDisabled = false; // 
      characterInputRea.saveDisabled = true; // 
      await nextTick();
      context.emit('removeFromStorage', characterInputRea.buildname);
    };

    /**  */
    const ascensionRange = computed((): number[] => {
      return [0, 1, 2, 3, 4, 5, 6]; // 0-
    });

    /**  */
    const levelRange = computed((): number[] => {
      return ARRAY[characterInputRea.];
    });

    /**  */
    const constellationRange = computed((): number[] => {
      const max = getMaxConstellation(characterMaster.value);
      return Array.from({ length: max + 1 }, (_, i) => i); // 0-
    });

    /**  */
    const ascensionOnChange = async () => {
      const minLevel = levelRange.value[0];
      const maxLevel = levelRange.value[levelRange.value.length - 1];
      if (characterInputRea. < minLevel) {
        characterInputRea. = minLevel;
      } else if (characterInputRea. > maxLevel) {
        characterInputRea. = maxLevel;
      }
      await nextTick();
      characterOnChange();
    };

    /**  */
    const recommendationOnChange = async (value: string | undefined) => {
      if (!props.recommendationList || !value) return;
      await nextTick();
      const recommendation = props.recommendationList[Number(value)];
      context.emit('update:recommendation', recommendation);
    };

    /**  */
    const normalAttackLevelRange = computed(() => {
      const max = getMaxTalentLevel(characterMaster.value, '');
      return Array.from({ length: max }, (_, i) => i + 1); // 1-
    });

    /**   */
    const elementalSkillLevelRange = computed(() => {
      const max = getMaxTalentLevel(characterMaster.value, '');
      return Array.from({ length: max }, (_, i) => i + 1); // 1-
    });

    /**   */
    const elementalBurstLevelRange = computed(() => {
      const max = getMaxTalentLevel(characterMaster.value, '');
      return Array.from({ length: max }, (_, i) => i + 1); // 1-
    });

    /**  */
    const weaponAscensionRange = computed(() => {
      let max = 6;
      if (weaponMaster.value. < 3) max = 4;
      return Array.from({ length: max + 1 }, (_, i) => i); // 0-
    });

    /**  */
    const weaponLevelRange = computed((): number[] => {
      return ARRAY[characterInputRea.];
    });

    /**  */
    const weaponRefineRange = computed((): number[] => {
      let max = 5;
      if (weaponMaster.value. < 3) max = 1;
      return Array.from({ length: max }, (_, i) => i + 1); // 1-
    });

    /**  */
    const weaponAscensionOnChange = async () => {
      const minLevel = weaponLevelRange.value[0];
      const maxLevel = weaponLevelRange.value[weaponLevelRange.value.length - 1];
      if (characterInputRea. < minLevel) {
        characterInputRea. = minLevel;
      } else if (characterInputRea. > maxLevel) {
        characterInputRea. = maxLevel;
      }
      await nextTick();
      weaponOnChange();
    };

    const artifactSetIndexRef = ref(0);
    const artifactSetSelectClass = (index: number) =>
      props.artifactSetSelectVisible && index == artifactSetIndexRef.value
        ? ' selected'
        : '';
    const openArtifactSetSelect = (index: number) => {
      artifactSetIndexRef.value = index;
      context.emit('open:artifact-set-select', index);
    };

    /**  */
    const characterOnChange = async () => {
      const workInput = {} as any;
      workInput. = characterInputRea.;
      workInput. = characterInputRea.;
      workInput. = characterInputRea.;
      workInput. = characterInputRea.;
      workInput. = characterInputRea.;
      workInput. = characterInputRea.;
      await nextTick();
      context.emit('update:character-input-character', workInput);
    };

    /**  */
    const weaponOnChange = async () => {
      const workInput = {} as any;
      workInput. = characterInputRea.;
      workInput. = characterInputRea.;
      workInput. = characterInputRea.;
      await nextTick();
      context.emit('update:character-input-weapon', workInput);
    };

    return {
      displayName,
      targetValue,
      getArtifactIconUrl,

      displayBuildName,
      visionSrc,
      bgImageClass,
      colorClass,
      bgColorClass,

      characterInputRea,
      characterMaster,
      weaponMaster,
      artifactSetMasters,
      recommendationOnChange,
      saveDisabled,
      saveOnClick,
      removeOnClick,
      ascensionRange,
      levelRange,
      constellationRange,
      ascensionOnChange,
      weaponAscensionRange,
      weaponLevelRange,
      weaponRefineRange,
      weaponAscensionOnChange,
      storageOrRecommendationRef,
      normalAttackLevelRange,
      elementalSkillLevelRange,
      elementalBurstLevelRange,
      openArtifactSetSelect,
      artifactSetSelectClass,
      characterOnChange,
      weaponOnChange,
      IMG_SRC_DUMMY,
    };
  },
});
